@import "variables.scss";

.advantages {
  max-width: 1920px;
  margin-top: 90px;

  &-container {
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 15px;

    @media (max-width: 1170px) {
      padding-left: 25px;
      padding-right: 25px;
    }
  }

  &__main {
    margin-top: 30px;
  }

  &__advantage-list {
    display: flex;
    justify-content: space-between;
  }

  &__advantage-elements {
    max-width: 560px;
    padding: 40px 30px;
    border: 1px solid #f1f1f1;
    margin-bottom: 30px;
    display: flex;
  }

  &__advantage-image {
    width: 60px;
    height: 60px;
  }

  &__advantage-body {
    margin-left: 15px;
  }

  &__advantage-title {
    font-size: 18px;
    font-weight: bold;
  }

  &__advantage-text {
    font-size: 14px;
    margin-top: 10px;
  }
}